
import styles from '../../../Modals/Modal.module.scss';
import  {AiFillCloseCircle} from 'react-icons/ai'
import { ModalPropsTestEdit } from 'src/default/interfaces/Interfaces';
import { useState, useContext } from 'react';
import Button from 'src/components/Button/Button';
import { Title } from 'src/components/Title/Title';
import { useForm } from 'src/hooks/useForm';
import React from 'react';
import { UserContext } from 'src/context/UserContext';
import { Input } from 'src/components/Input/Input';
import {TextField} from '@mui/material';
import {LoadingButton} from '@mui/lab';

export const ChangePasswordModal = ({openModal, setModal, data, edit}: ModalPropsTestEdit) => {

  const [editedPassword, setEditedPassword] = useState<string>("");
  const [newPassword, setNewPassword] = useState<string>("");
  const [email, setEmail] = useState<string>("");
  const [idUser, setIdUser] = useState<string>("");
  const [loading, setLoading] = useState<Boolean>(false);
  const [message, setMessage] = useState<string>('');
  const [error, setError] = React.useState<String>("");



    const {
        userLogin,
        recoveryPassword,
        usuario
    } = useContext( UserContext );


    const handleSubmit = async (e: any) => {
        e.preventDefault();
        if(email == usuario?.email){
            const fetchRecoveryPassword = fetch(`https://nestrental.com.br:80/api/usuarios/recovery`,{
                method: 'POST',
                headers:{
                    'Content-Type': 'application/json'
                }, body: JSON.stringify({
                    email: email,
                    url: 'https://nestrental.com.br/redefinir-senha'
                })
            })

            const response = await fetchRecoveryPassword;
            if(response.status == 200){
                const json = await response.json();
                setMessage('Email enviado com sucesso. Por favor, Verifique sua caixa de email!');
                setError('');
                window.localStorage.setItem('token_password', json.token);
            }else if(response.status == 403){
                setError('Este usuário não existe!')
                setMessage('');
            }

        }else{
          setError('Email não corresponde a esta conta.')
          setMessage('');
        }
    }


  return (
    <>
      {openModal && (
        <div className={styles.modalCategory} style={{background: '#fff'}}>
          <div className={styles.modal__divCloseButton}>
            <button 
                className={styles.modal__divCloseButton__closeButton}
                onClick={(e)=>setModal(!openModal)}
              >
              <AiFillCloseCircle/>
            </button>
          </div>
          
          <Title>Editar senha</Title>
          <form onSubmit={edit && handleSubmit}>
            <div style={{marginTop: '1rem'}}>
              <TextField
                label="E-mail para redefinição de senha"
                placeholder="Digite o email"
                value={email}
                onChange={(e)=>setEmail(e.target.value)}
                size="medium"
                fullWidth={true}
                color="primary"
              />
            </div>
            <div style={{display: 'flex', gridGap: '1rem', width: '20rem', marginTop: '1rem'}}>

              <LoadingButton
                type="submit"
                color="primary"
                variant="contained"
                onClick={(e)=>handleSubmit(e)}
                loading={loading?true:false}
              >
                Editar
              </LoadingButton>

              <LoadingButton
                type="button"
                color="warning"
                variant="contained"
                onClick={(e)=>setModal(!openModal)}
                loading={loading?true:false}
              >
                Cancelar
              </LoadingButton>
            </div>
            <div>

              {!loading && message && (
                <p style={{color: 'green'}}>{message}</p>
              ) }

              {!loading && error &&(
                <p style={{color: 'red'}}>{error}</p>
              ) }
            </div>
          </form>
            
        </div>
      )}
    </>
  )
}
