
import styles from '../../../Modals/Modal.module.scss';
import  {AiFillCloseCircle} from 'react-icons/ai'
import { Input } from 'src/components/Input/Input';
import React from 'react';
import { ModalPropsTestEdit } from 'src/default/interfaces/Interfaces';
import { Title } from 'src/components/Title/Title';
import { UserContext } from 'src/context/UserContext';
import {useContext} from 'react';
import http from 'src/http';
import SearchCep from 'src/helpers/SearchCep';
import {TextField} from '@mui/material'
import {LoadingButton} from '@mui/lab'



export const AddressPayModal = ({openModal, setModal, data, edit}: ModalPropsTestEdit) => {


  /*PAY ADDRESS*/

  //street
  const [payEditedStreet, setPayEditedStreet] = React.useState<string>('');
  const [payNewStreet, setPayNewStreet] = React.useState<string>('');


  //neighbourhood
  const [payEditedNeighbourhood, setPayEditedNeighbourhood] = React.useState<string>('');
  const [payNewNeighbourhood, setPayNewNeighbourhood] = React.useState<string>('');

  //city
  const [payEditedCity, setPayEditedCity] = React.useState<string>('');
  const [payNewCity, setPayNewCity] = React.useState<string>('');

  //state
  const [payEditedState, setPayEditedState] = React.useState<string>('');
  const [payNewState, setPayNewState] = React.useState<string>('');

  //number of address
  const [payEditedNumberAddress, setPayEditedNumberAddress] = React.useState(null);
  const [payNewNumberAddress, setPayNewNumberAddress] = React.useState(null);

  //complement
  const [payEditedComplement, setPayEditedComplement] = React.useState<string>('');
  const [payNewComplement, setPayNewComplement] = React.useState<string>('');

  //cep
  const [payEditedCep, setPayEditedCep] = React.useState('');
  const [payNewCep, setPayNewCep] = React.useState('');

  //nome responsável
  const [nameEditedResp, setNameEditedResp] = React.useState<string>('');
  const [nameNewResp, setNameNewResp] = React.useState<string>('');



  const [loading, setLoading] = React.useState<Boolean>(false);
  const [message, setMessage] = React.useState<string>('');
  const [error, setError] = React.useState<string>('');
  const [addressUser, setAddressUser] = React.useState(null);
  const [idAddress, setIdAddress] = React.useState(null);


  const {
    usuario
  } = useContext(UserContext)

  async function buscaCepBilling() {
    const json = await SearchCep(payNewCep);
    if(json){
      setPayNewStreet(json.logradouro);
      setPayNewCity(json.localidade);
      setPayNewState(json.uf)
      setPayNewNeighbourhood(json.bairro)
    }else{
      setPayNewStreet('');
      setPayNewCity('');
      setPayNewState('')
      setPayNewNeighbourhood('')
    }
  }

 

  async function buscar_cidade(nome_cidade: string) {
    const nome = encodeURI(nome_cidade);
    const { data } = await http.get(`/cidades/${nome}`);
    return data?.id;
  } 


  async function handleSubmit(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault();
    setLoading(true);
    http.put(`/enderecos/${addressUser?.entidade?.enderecos[0]?.id_endereco}`, {
        "id_cidade": await buscar_cidade(payNewCity),
        "cep": payNewCep,
        "bairro": payNewNeighbourhood,
        "complemento": payNewComplement? payNewComplement : 'Não possui',
        "rua": payNewStreet,
        "numero": payNewNumberAddress,
        "contato": "",
        "telefone": "",
        "email": "",
        "nome_obra": "",
        tipo: "C"
    })
    .then(response=>{
      setMessage("Endereço editado com sucesso.")
      window.location.reload();
    })
    .catch(error=>{
      console.log(error)
    })
    setLoading(false)
  }


  async function getUserById(){
    const teste = fetch(`https://nestrental.com.br:80/api/usuarios/${usuario?.id}`, {
      headers:{
        'Content-Type': 'application/json',
      }, 
      method: 'GET',
    })
  
    const data = await teste;
    const dataJson = await data.json();
    setIdAddress(dataJson.entidade.enderecos[0].id);
    setAddressUser(dataJson)
  }

  function getAddressUser() {
    setPayNewCep(addressUser?.entidade?.enderecos[0].endereco.cep)
    setPayNewStreet(addressUser?.entidade?.enderecos[0].endereco.rua);
    setPayNewCity(addressUser?.entidade?.enderecos[0].endereco.cidade.nome);
    setPayNewState(addressUser?.entidade?.enderecos[0].endereco.cidade.estado.nome)
    setPayNewNeighbourhood(addressUser?.entidade?.enderecos[0].endereco.bairro)
    setPayNewNumberAddress(addressUser?.entidade?.enderecos[0].endereco.numero)
  }



  
  React.useEffect(()=>{
    getUserById();
    getAddressUser()
  }, [])

  React.useEffect(()=>{
    buscaCepBilling()
  },[payNewCep && payNewCep.length === 8])
  


  return (
    <>
      {openModal && (
        <div className={styles.modal} style={{background: '#fff', height: '400px'}}>
          <div className={styles.modal__divCloseButton}>
          <button 
              className={styles.modal__divCloseButton__closeButton}
              onClick={(e)=>setModal(!openModal)}
            >
              <AiFillCloseCircle/>
            </button>
          </div>

          <section>
              <form onSubmit={(e)=>handleSubmit(e)}>
                  <div className={styles.formCheckout__div}>
                    <Title level={3}>
                        Detalhes do faturamento
                    </Title>
                    <div style={{display: "flex", justifyContent: "space-between", gridGap: "1rem"}}>
                        <div  style={{width:"100%"}}>

                            <TextField 
                                type="number" 
                                label="CEP"
                                id="cep_client" 
                                required={true}
                                name="cep_client" 
                                placeholder="Digite seu CEP"
                                value={payNewCep}
                                onChange={(e)=>setPayNewCep(e.target.value)}
                                color="primary"
                                fullWidth={true}
                                
                              
                            />
                        </div>
                        <div  style={{width:"100%"}}>
                            <TextField 
                                type="text" 
                                id="street_client" 
                                required={true}
                                label="Rua/Av"
                                name="street_client"  
                                placeholder="Digite sua rua ou avenida"
                                value={payNewStreet}
                                onChange={(e)=>setPayNewStreet(e.target.value)}
                                color="primary"
                                fullWidth={true}
                                
                            />
                        </div>
                        <div  style={{width:"100%"}}>
                            <TextField 
                                type="number" 
                                id="number_client" 
                                label="Númeroº"
                                required={true}
                                color="primary"
                                fullWidth={true}
                                placeholder="Digite o número"
                                name="number_client"
                                value={payNewNumberAddress}
                                onChange={(e)=>setPayNewNumberAddress(e.target.value)}
                            />
                        </div>
                    </div>

                    <div style={{display: "flex", justifyContent: "space-between", gridGap: "1rem", marginTop: '1rem'}}>
                        <div style={{width:"100%"}}>
                            <TextField 
                                type="text" 
                                id="country_client"
                                label="Cidade"
                                required={true}
                                placeholder="Digite sua cidade"
                                name="country_client" 
                                value={payNewCity}
                                onChange={(e)=>setPayNewCity(e.target.value)}
                                color="primary"
                                fullWidth={true}
                            />
                        </div>

                        <div style={{width:"100%"}}>

                            <TextField 
                                label="Estado"
                                type="text" 
                                id="state_client" 
                                required={true}
                                placeholder="Digite seu estado"
                                name="state_client" 
                                value={payNewState}
                                onChange={(e)=>setPayNewState(e.target.value)}
                                color="primary"
                                fullWidth={true}
                            />
                        </div>

                        <div style={{width:"100%"}}>
                            <TextField 
                                type="text" 
                                label="Bairro"
                                id="neighbour_client" 
                                placeholder="Digite seu bairro"
                                name="neighbour_client" 
                                required={true}
                                value={payNewNeighbourhood}
                                onChange={(e)=>setPayNewNeighbourhood(e.target.value)}
                                color="primary"
                                fullWidth={true}
                            />
                        </div>
                        <div style={{width:"100%"}}>
                           
                            <TextField 
                                type="text" 
                                label="Complemento"
                                id="complement_client" 
                                name="complement_client"
                                placeholder="Digite um complemento"
                                value={payNewComplement}
                                onChange={(e)=>setPayNewComplement(e.target.value)}
                                color="primary"
                                fullWidth={true}
                            />
                        </div>
                    
                    </div>
                    </div>
                  <div style={{display: 'flex', gridGap: '1rem', width: '20rem', marginTop: '1rem'}}>
                    <LoadingButton 
                        type="submit" 
                        variant="contained"
                        color="primary"
                        loading={loading? true:false}
                    >
                      Editar
                    </LoadingButton>

                    <LoadingButton 
                        type="button" 
                        variant="contained"
                        color="warning"
                        value="Cancelar"
                        onClick={(e)=>setModal(!openModal)}
                      >
                        Cancelar
                      </LoadingButton>
                  </div>
                 
              {!loading && message && (
                <p style={{color: 'green'}}>{message}</p>
              ) }

              {!loading && error &&(
                <p style={{color: 'red'}}>{error}</p>
              ) }

              </form>
          </section>
        </div>
      )}
    </>
  )
}
